.product {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    margin: 10px;
    padding: 20px;
    width: 100%;
    max-height: 400px;
    min-width: 100px;
    background-color: white;
    z-index: 1;
  }
  
  .product__rating {
    display: flex;
  }
  
  .product > img {
    max-height: 200px;
    width: 100%;
    object-fit:scale-down;
    margin-bottom: 15px;
    justify-content: center;
  }
  
  .product > button {
    background: #f0c14b;
    border: 1px solid;
    margin-top: 10px;
    border-color: #a88734 #9c7e31 #846a29;
    color: #111;
    border-radius: 4px;
  }
  
  .product__price {
    margin-top: 5px;
  }
  
  .product__info {
    height: 100px;
    margin-bottom: 15px;
  }

  .button:hover{
    background: #fcce5a;
  }

  .product:hover{
    transform:scale(1.02);
    transition-duration: 0.30s;
    box-shadow: 0px 10px 20px 0px black;
  }

 .input_txt{
  border: 2px solid grey;
  border-radius: 4px;
